import { useState, useEffect } from "react";
import React from "react";

import "../App.css";

import supabase from "../supabaseClient";
import { render } from "@testing-library/react";

const Home = () => {
  // useState
  const [users, setUsers] = useState([]);

  const [user, setUser] = useState({
    name: "",
    age: "",
    graduation: "",
  });

  const [user2, setUser2] = useState({
    id: "",
    name: "",
    age: "",
    graduation: "",
  });

  console.log(user2);

  // useEffect for rendering page

  useEffect(() => {
    fetchUsers();
  }, []);

  // Fetching Users Data

  async function fetchUsers() {
    const { data } = await supabase.from("users").select("*");
    setUsers(data);
  }

  // Handling Data when Creating User data

  function handleChange(event) {
    setUser((prevFormData) => {
      return {
        ...prevFormData,
        [event.target.name]: event.target.value,
      };
    });
  }

  // Handling Data when Updagting User data

  function handleChange2(event) {
    setUser2((prevFormData) => {
      return {
        ...prevFormData,
        [event.target.name]: event.target.value,
      };
    });
  }

  // Function for Creating Users

  async function createUser() {
    await supabase
      .from("users")
      .insert({ name: user.name, age: user.age, graduation: user.graduation });

    fetchUsers();
  }

  // Function for deleting Users and their data

  async function deleteUser(userId) {
    const { data, error } = await supabase
      .from("users")
      .delete()
      .eq("id", userId);

    fetchUsers();

    if (error) {
      console.log(error);
    }

    if (data) {
      console.log(data);
    }
  }

  // Function for Displaying user's data after Updating User's Data

  function displayUser(userId) {
    users.map((user) => {
      if (user.id == userId) {
        setUser2({
          id: user.id,
          name: user.name,
          age: user.age,
          graduation: user.graduation,
        });
      }
    });
  }

  // Function for Updating User's Data

  async function updateUser(userId) {
    const { data, error } = await supabase
      .from("users")
      .update({
        id: user2.id,
        name: user2.name,
        age: user2.age,
        graduation: user2.graduation,
      })
      .eq("id", userId);

    fetchUsers();

    if (error) {
      console.log(error);
    }

    if (data) {
      console.log(data);
    }
  }

  return (
    <div className="bg-slate-300 text-center sm:text-justify shadow-md pt-8 lg:p-2 rounded-xl">
      {/* FORM 1 for Creating Users*/}
      <form onSubmit={createUser} className="space-y-2 mb-2 lg:space-x-1">
        <input
          className="border-2 p-1"
          type="text"
          placeholder="Name"
          name="name"
          onChange={handleChange}
        />
        <input
          className="border-2 p-1"
          type="number"
          placeholder="Age"
          name="age"
          onChange={handleChange}
        />
        {/* Graduation  */}
        <input
          className="border-2 p-1"
          type="text"
          placeholder="Graduation"
          name="graduation"
          onChange={handleChange}
        />
        <button
          type="submit"
          className="bg-red-300 shadow-md hover:border-[1px] hover:border-[#646cff] px-2 py-1 rounded-lg transition-all cursor-pointer mx-auto block sm:inline"
        >
          Create
        </button>
      </form>

      <hr className="mb-2 lg:m-0 border-slate-400 shadow-md" />

      {/* FORM 1 for Updating Users*/}

      <form
        onSubmit={() => updateUser(user2.id)}
        className="space-y-2 lg:space-x-1"
      >
        <input
          className="border-2 p-1"
          type="text"
          name="name"
          onChange={handleChange2}
          defaultValue={user2.name}
        />
        <input
          className="border-2 p-1"
          type="number"
          name="age"
          onChange={handleChange2}
          defaultValue={user2.age}
        />
        {/* Graduation  */}
        <input
          className="border-2 p-1"
          type="text"
          name="graduation"
          onChange={handleChange2}
          defaultValue={user2.graduation}
        />
        <button
          type="submit"
          className="bg-red-300 shadow-md hover:border-[1px] hover:border-[#646cff] px-2 py-1 rounded-lg transition-all cursor-pointer mx-auto block sm:inline"
        >
          Save Changes
        </button>
      </form>

      <table className="text-center ">
        <thead>
          <tr>
            <th>Name</th>
            <th>Age</th>
            <th>Actions</th>
            <th>Graduation</th>
          </tr>
        </thead>

        <tbody className="font-semibold capitalize shadow-md">
          {users.map((user) => (
            <tr key={user.id}>
              <td>{user.name}</td>
              <td>{user.age}</td>
              <td>{user.graduation}</td>

              <td>
                <button
                  className="bg-red-300 shadow-md hover:border-[1px] hover:border-[#646cff] px-2 py-1 rounded-lg transition-all cursor-pointer mx-1"
                  onClick={() => {
                    deleteUser(user.id);
                  }}
                >
                  Delete
                </button>
                <button
                  className="bg-red-300 shadow-md hover:border-[1px] hover:border-[#646cff] px-2 py-1 rounded-lg transition-all cursor-pointer mx-1"
                  onClick={() => {
                    displayUser(user.id);
                  }}
                >
                  Edit
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Home;
